<template>
  <div id="app">
    <div>
      <nav class="navbar navbar-light bg-light">
        <form class="form-inline">
          <input
            class="form-control mr-sm-2"
            type="search"
            id="id"
            placeholder="Masukkan ID"
            aria-label="Search"
            v-model="CekData"
          />
          <button
            class="btn btn-outline-success my-2 my-sm-0"
            @click="findItemById"
            :disabled="!CekData || find == true"
          >
            Search
          </button>
        </form>
      </nav>

      <!-- Form Pembuatan data -->
      <div class="container">
        <h1 class="text-info text-center">My List Todo</h1>
        <div class="row">
          <div class="col-lg-12 data-input">
            <div class="card mx-auto">
              <div class="card-body">
                <form>
                  <div class="form-group text-center">
                    <label for="Deskripsi">Input data: </label>
                    <input
                      type="text"
                      id="deskripsi"
                      name="deskripsi"
                      placeholder="Input Data"
                      class="form-control mb-2"
                      v-model="deskripsi"
                    />
                    <label for="Jumlah">Jumlah</label>
                    <input
                      type="text"
                      id="jumlah"
                      placeholder="Masukkan Jumlah"
                      class="form-control"
                      v-model="jumlah"
                    />
                  </div>
                </form>
                <button
                  class="btn btn-primary"
                  @click="addItem"
                  :disabled="!deskripsi"
                >
                  Submit
                </button>

                <button
                  class="btn ml-2"
                  type="button"
                  data-toggle="collapse"
                  data-target="#alldata"
                  aria-controls="#alldata"
                  aria-expanded="false"
                  v-if="!find == true"
                >
                  All data
                </button>
              </div>
            </div>
          </div>

          <!-- Tabel data yang sudah dibuat -->
          <div class="col-lg-12 col-sm-12">
            <ul class="collapse show list-group" id="alldata">
              <div v-if="!find == true">
                <li
                  class="list-group-item my-3"
                  v-for="(item, i) in items"
                  :key="item._id"
                  id=""
                >
                  <button
                    type="button"
                    class="btn btn-danger float-right ml-2"
                    @click="isSelected(item) ? unselect() : deleteItem(item, i)"
                  >
                    {{ isSelected(item) ? "Cancel" : "Delete" }}
                  </button>
                  <button
                    type="button"
                    class="btn btn-info float-right"
                    @click="
                      isSelected(item) ? updateItem(item, i) : select(item)
                    "
                  >
                    {{ isSelected(item) ? "Save" : "Edit" }}
                  </button>
                  <form v-if="isSelected(item)">
                    <input type="text" v-model="editDeskripsi" />
                  </form>
                  <span v-else>
                    <span>{{ item._id }}</span> <br />
                    <span
                      ><strong>{{ item.deskripsi }}</strong></span
                    >
                    <br />
                    <span>{{ item.date }}</span> <br />
                    <span> Jumlah : {{ item.jumlah }} </span>
                  </span>
                </li>
              </div>

              <div v-if="find == true">
                <h5 class="text-info my-2">{{ findMessage }}</h5>
                <li
                  class="list-group-item my-3"
                  v-for="result in findItem"
                  :key="result._id"
                >
                  <button
                    class="btn btn-danger float-right"
                    @click="unselectSearch()"
                  >
                    Back
                  </button>
                  <form action="">
                    <span>ID : {{ result._id }}</span> <br />
                    <span>Kegiatan : {{ result.deskripsi }}</span> <br />
                    <span>Jumlah : {{ result.jumlah }}</span> <br />
                  </form>
                </li>
              </div>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "App",
  data() {
    return {
      items: [],
      CekData: "",
      CekDesk: "",
      findItem: [],
      deskripsi: "",
      findMessage: "",
      find: false,
      jumlah: "",
      editDeskripsi: "",
      selected: {},
    };
  },
  // Menghubungkan api dengan app front-end
  async mounted() {
    axios.get("/api/todolistitems").then((response) => {
      this.items = response.data.data;
    });

    // const response = await axios.get("/api/todolistitems");
    // this.items = response.data.data;
  },
  methods: {
    // async allItem() {
    //   axios.get("/api/todolistitems/").then((response) => {
    //     this.items = response.data.data;
    //   });
    // },

    async getItemByName() {
      const response = await axios.get(
        "/api/todolistitems/?deskripsi=" + this.CekDesk
      );
      this.findItem = response.data;
    },

    async findItemById() {
      axios
        .get("/api/todolistitems/" + this.CekData)
        .then((result) => {
          this.findItem.push(result.data);
          this.find = true;
          this.findMessage = "Data yang ditemukan";
        })
        .catch((err) => {
          alert("Data Tidak ada");
          console.log(err);
        });
    },

    // Mengirimkan data menggunakan metode post
    async addItem() {
      axios
        .post("api/todolistitems", {
          deskripsi: this.deskripsi,
          jumlah: this.jumlah,
        })
        .then((response) => {
          this.items.push(response.data.data);
          this.deskripsi = "";
          this.jumlah = "";
        })
        .catch((err) => {
          alert("Masukkan data dengan benar");
          console.log(err);
        });
      // const response = await axios.post("api/todolistitems", {
      //   deskripsi: this.deskripsi,
      //   jumlah: this.jumlah,
      // });
      // this.items.push(response.data.data);
      // this.deskripsi = "";
      // this.jumlah = "";
    },

    // Menghapus data menggunakan metode delete
    async deleteItem(item, i) {
      // axios.delete("/api/todolistitems").then((response) => {
      //   this.items.splice(i, 1);
      // });
      await axios.delete("/api/todolistitems/" + item._id);
      if (confirm("Apakah anda yakin?")) {
        this.items.splice(i, 1);
      } else {
        this.unselect();
      }
    },

    // Mengupdate data menggunakan metode put
    async updateItem(item, i) {
      const response = await axios.put("/api/todolistitems/" + item._id, {
        deskripsi: this.editDeskripsi,
      });

      if (confirm("Apakah anda yakin untuk mengubah data ini?")) {
        this.items[i] = response.data.data;
        this.unselect();
      } else {
        alert("Operasi dibatalkan");
        this.unselect();
      }
    },
    select(item) {
      this.selected = item;
      this.editDeskripsi = item.deskripsi;
    },
    isSelected(item) {
      return this.selected._id === item._id;
    },
    unselect() {
      (this.selected = {}), (this.editDeskripsi = {});
    },
    unselectSearch(findItem, i) {
      this.findItem.splice(i, 1);
      this.find = false;
      this.CekData = "";
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.list-enter-active,
.list-leave-active {
  transition: all 0.5s ease;
}
.list-enter-from,
.list-leave-to {
  opacity: 0;
}
</style>
